@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap");
.bb--yellow {
  border-bottom: 2px solid #ffdd00;
}

.topmenu {
  background-color: #1a1a1a;
  color: #a9a9a9;
  font-family: "Lato", sans-serif;
}
.topmenu .topmenu__container {
  display: flex;
  width: 80%;
}
.topmenu .topmenu__container .topmenu__item {
  display: flex;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0px;
  padding: 0px;
  width: 100%;
}
.topmenu .topmenu__container .topmenu__item .topmenu__itemlink {
  display: flex;
  justify-content: center;
  margin: 0px 5px;
  font-size: 0.8em;
}

.primarymenu {
  background-color: #101000;
  color: #ffffff;
  font-family: "Lato", sans-serif;
}
.primarymenu .primarymenu__container {
  display: flex;
  width: 80%;
}
.primarymenu .primarymenu__container .primarymenu__logo {
  width: 40%;
  margin-left: 20%;
}
.primarymenu .primarymenu__container .primarymenu__logo .primarymenu__logoimage {
  width: 60%;
}
.primarymenu .primarymenu__container .primarymenu__item {
  display: flex;
  width: 60%;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0px;
  padding: 0px;
}
.primarymenu .primarymenu__container .primarymenu__item .primarymenu__itemlink {
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  font-size: 1em;
  text-align: center;
  align-items: center;
}
.primarymenu .primarymenu__container .primarymenu__item .primarymenu__itemlink .itemlink__search {
  width: 100%;
  color: #ffffff;
}

body {
  margin: 0px;
}